@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", sans-serif;
}
a {
  text-decoration: none;
  color: #33333a;
}
ul {
  list-style: none;
}

body {
  background-color: white;
}
button {
  cursor: pointer;
  border: none;
}
